// src/app/[lineId]/ESOP_Dashboard/settings/page.jsx
import { DroneEarlyInformSettings } from "@/features/drone-early-inform/components/DroneEarlyInformSettings"

export default async function Page({ params }) {
  const { lineId: raw } = await params
  const lineId = Array.isArray(raw) ? raw[0] : raw ?? ""

  return (
    <div className="pb-10">
      <DroneEarlyInformSettings lineId={lineId} />
    </div>
  )
}
